.collection {
}

#miniShowCase {
  background-color: #f5b041;
  padding-bottom: 30px;
  min-height: 314px;
}

#miniCard {
  height: 175px;
  width: 85px;
  border: 2px solid;
}

#miniCard > .card-title {
  height: 115px;
  overflow: auto;
  margin: 5px;
}

div#toolbar {
  height: auto;
  border-top: 6px solid black;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  background-color: #a2cd48;
  background-image: url("");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

div#toolbar h1 {
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

div#cardCollection {
  height: auto;
  color: #fff;
  border: 6px solid black;
  overflow: auto;
  background-image: url("../images/trades.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

div#minirow1 {
  margin-bottom: 15px;
}

div#minirow2 {
  padding: 20px;
}

input[type="number"] {
  width: 60px;
  border: 2px solid;
  padding-left: 13px;
  border-radius: 5px;
}
