@import url(https://fonts.googleapis.com/css?family=Bangers);
html {
  min-height: 100% !important;
}

body {
  font-family: "Bangers", cursive !important;
  min-height: 100% !important;
}

button.btn-primary {
  background-color: #ec7063;
  box-shadow: 2px 2px black;
  border: 2px solid black;
  border-radius: 5px;
  margin: 5px;
}

.home {
  background-image: url(/static/media/panther.18e52e7c.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  height: 100vh;
  border-top: 6px solid black;
}

.welcomeMessage {
  border-left: 6px solid black;
  border-right: 6px solid black;
  height: 400px;
}

.welcomeMessage h1 {
  margin-top: 100px;
}

.trades {
  height: 400px;
  border-top: 6px solid black;
  border-left: 6px solid black;
  border-right: 3px solid black;
  border-bottom: 0px;
  background-image: url(/static/media/thanos.ab4248c5.jpg);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.myCollection {
  height: 400px;
  border: 6px solid black;
  border-left: 3px solid black;
  border-bottom: 0px;
  background-image: url(/static/media/trades.e44e867c.jpg);

  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.home h1,
.home h2,
.home h3,
.home h4 {
  margin: 10px;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}

.showcase {
  border: 6px solid BLACK;

  background-image: url(/static/media/venom.62eeb1a8.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#showCard {
  transition: all 0.2s ease-in-out;
  color: black;
  border: 2px solid;
  margin: 25px;
  width: 18rem;
}

#showCard:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

#showCard img {
  height: 250px;
  border-bottom: 2px solid;
}

.card-common .card-body {
  background-color: #a6acaf;
}

.card-uncommon .card-body {
  background-color: #28b463;
}

.card-rare .card-body {
  background-color: #3498db;
}

.card-epic .card-body {
  background-color: #8e44ad;
}

.card-legendary .card-body {
  background-color: #f39c12;
}

a.btn-primary {
  border: 2px solid black;
  box-shadow: 2px 2px black;
  background-color: #ec7063;
}

li.list-group-item {
  color: black;
}

.card {
  border: 2px solid black;
}

.collection {
}

#miniShowCase {
  background-color: #f5b041;
  padding-bottom: 30px;
  min-height: 314px;
}

#miniCard {
  height: 175px;
  width: 85px;
  border: 2px solid;
}

#miniCard > .card-title {
  height: 115px;
  overflow: auto;
  margin: 5px;
}

div#toolbar {
  height: auto;
  border-top: 6px solid black;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  background-color: #a2cd48;
  background-image: url("");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

div#toolbar h1 {
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

div#cardCollection {
  height: auto;
  color: #fff;
  border: 6px solid black;
  overflow: auto;
  background-image: url(/static/media/trades.e44e867c.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

div#minirow1 {
  margin-bottom: 15px;
}

div#minirow2 {
  padding: 20px;
}

input[type="number"] {
  width: 60px;
  border: 2px solid;
  padding-left: 13px;
  border-radius: 5px;
}

div#searchRow {
  border-top: 6px solid;
  border-left: 6px solid;
  border-right: 6px solid;
  background-color: #f1948a;
}

div#resultRow {
  color: #fff;
  border: 6px solid black;
  height: 100%;
  background-image: url(/static/media/panther.18e52e7c.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.form-inline {
  margin-top: 20px;
}

input[type="search"] {
  border: 2px solid;
  height: 40px;
  width: 200px;
  margin-left: 30px;
  margin-right: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
}

select.marvelType {
  height: 40px;
  width: 200px;
  margin-left: 30px;
  margin-right: 50px;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 2px solid;
}

button#searchButton {
  width: 200px;
  margin-left: 30px;
}

div#characterResult {
  padding: 25px;
}

div#resultCard {
  border: 4px solid black;
  margin: 15px;
}

div#resultCard img {
  border-bottom: 4px solid black;
}

div#resultCard .card-body {
  background-color: #e5e8e8;
  color: black;
}

p.card-text {
  height: 75px;
  overflow: auto;
}

h5.card-title {
  height: 50px;
  overflow: hidden;
  border-bottom: 4px solid black;
}

button#homeButton {
  margin-top: 30px;
  margin-right: 30px;
  margin-left: 16px;
  margin-bottom: 20px;
  width: 250px;
  height: 50px;
}

div#details {
  background-color: #e5e8e8;
  height: 100vh;
}

div#title_row {
  border-bottom: 4px solid black;
}

.info {
  height: 55vh;
}

.info > .col-lg-3 {
  border: 4px solid;
}

.bg {
  background-image: url(/static/media/login.e1a6fcda.jpg);
  height: 94vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.main-section {
  margin: 0 auto;

  margin-top: 15vh;
  padding: 0;
}

#modal-content {
  background-color: #2980b9;
  opacity: 0.95;
  padding: 0 18px;
  box-shadow: 0px 0px 3px;
}

.user-img {
  margin-top: -60px;
}

.user-img img {
  height: 100px;
  width: 100px;
  border-radius: 5px;
  margin-bottom: 25px;
}

.form-group {
  margin-bottom: 25px;
}

.form-group input {
  height: 42px;
  border-radius: 5px;
  border: 0;
  font-size: 18px;
}

button {
  width: 50%;
}

#btn {
  margin: 10px;
  background-color: #27c2a5;
  color: #fff;
  font-size: 19px;
  padding: 7px 14px;
  margin-bottom: 25px;
  border-radius: 5px;
  border-bottom: 4px solid #219882;
}

#btn:hover,
#btn:focus {
  background-color: #25a890;
  border-bottom: 4px solid #25a890;
}

.forgot {
  padding: 5px 0 25px;
}

.forgot a {
  color: #c2fbfe;
}

